	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early morning, I read \"The Third Eye to See China\", which is written by a German, called Luo Yininger. It is an expert on Chinese issues. I always feel that it is not so German, or it is too Chinese. However, this book's analysis of many problems in China has its own perspective. Basically, it looks at some issues in the Western perspective. In China's position, it is a balance to look at some issues, so it is called \"the third eye.\" \". May be two</p>\n<p>Take half of each eye. In the final analysis of the future of Chinese society, the author said:</p>\n<p>\"A society in reform is extremely fragile. The old system is so hard that it can hardly continue to function. The stable social function of the new system has not yet fully formed. It is difficult for society to maintain stability even in a slight impact. China is a With a huge social system of 100 million people, new information flows in and completes its acceptance process, and it takes more than a year to achieve reorganization. The distance in time causes the system to actually split and inequality.\"</p>\n<p>In fact, more than that. However, these are also very important. In the process of modernization, we must not pay high attention. The problem is that in development, paying attention to objective problems and paying attention to subjective problems is a contradiction. In many cases, it is more important to pay attention to subjective contradictions. Only when subjective contradictions do not affect the modernization process can objective contradictions be overcome by concentrated forces.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0112.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0112.md","frontmatter":{"title":"Sun on Sunday, June 12"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0112.md absPath of file >>> MarkdownRemark"}}

